<template>
  <!-- 笔记列表页面 -->
  <div class="notesBox">
    <p class="title">
      学习笔记
    </p>

    <!-- 布局盒子 -->
    <div class="notes_layout">
      <!-- 滚动盒子 -->
      <ScrollContainer @arrive="arrive">
        <ul class="notesList">
          <!-- item.name 是组件对象需要在获取里面name -->
          <li
            v-for="(item, index) in article"
            :key="item.id"
            @click="intoDetails(index, item.date, item.type)"
          >
            <p>{{ item.describe }}</p>
            <p>记录时间：{{ item.date }}</p>
          </li>
        </ul>
      </ScrollContainer>
    </div>
  </div>
</template>
<script>
import { ref, defineComponent, reactive } from "vue";
import { useRouter } from "vue-router";
// 导入文章列表

import { components } from "../article/index";
export default defineComponent({
  setup() {
    const router = useRouter();
    const article = reactive(components);
    const scrollbarRef = ref(null);
    // 滚动条触底事件
    const arrive = function (tb) {
      console.log(tb);
    };
    // 点击文章，传递name进入详情页面
    const intoDetails = function (index, date, tag) {
      // url中显示参数跳转
      router.push({
        name: "details",
        query: { article: index, date: date, tag: tag },
      });
    };

    return {
      scrollbarRef,
      article,
      intoDetails,
      arrive,
    };
  },
});
</script>
<style lang="less" scoped>
ul,
li,
p {
  padding: 0;
  margin: 0;
  list-style: none;
}
.notesBox {
  position: relative;
  height: 100%;
  .title {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding: 0 40px;
    box-sizing: border-box;
    line-height: 50px;
    font-style: italic;
    font-size: 20px;
    background: rgb(226, 232, 255);
  }
  .notes_layout {
    height: 100%;
    padding-top: 50px;
    box-sizing: border-box;
    .notesList {
      padding: 0px 40px;
      height: 100%;
      li {
        padding: 10px;
        transition: 0.3s;
        border-bottom: 1px solid #ddd;
        p:nth-child(1) {
          font-size: 16px;
          line-height: 40px;
        }
        p:nth-child(2) {
          font-size: 12px;
          color: rgb(185, 185, 185);
        }
        &:hover {
          cursor: pointer;
          color: rgb(0, 60, 255);
        }
      }
    }
  }
}
// 列表页标题
.min .notes_layout .notesList,
.min .title {
  padding: 0px 5px;
  line-height: 30px;
  font-size: 18px;
}
.min .notes_layout {
  padding-top: 30px;
}
// 例表页内容
.min .notes_layout .notesList li {
  padding: 5px 0px;
}
.min .notes_layout .notesList li p:nth-child(1) {
  line-height: 24px;
  font-size: 14px;
  overflow: hidden;
  text-overflow: ellipsis; /* 加省略号 */
  white-space: nowrap; /* 强制不换行 */
}
.min .notes_layout .notesList li p:nth-child(2) {
  line-height: 18px;
  font-size: 12px;
}
</style>
